
.container, section:not(:first-of-type),article,.page-header-content-wrapper {
	@extend %container;
}

html {

}
html,body {
	margin : 0 !important;
	padding : 0 !important;
	width : 100vw;
	height : 100%;
	max-width : 100vw;
	min-width : 300px;
	background-color : whitesmoke;
}
body {
	min-width : 100vw;
	min-height : 100vh;
	display : flex;
	flex-direction : column;
	justify-content : space-between;
	& > header:first-of-type {
		padding : 0;
		width : 100%;
		margin : 0;
		background-color : whitesmoke;
	}
	& > footer:last-of-type {
		width            : 100%;
		margin           : 0;
		padding          : 2em 0;
		background-color : whitesmoke;
		vertical-align   : middle;
		text-align       : center;
		
		p {
			font-size   : small;
			text-align  : center;
			line-height : 1;
			margin      : 0 auto;
			padding     : 0;
			font-weight : 500;
			color       : gray;
		}
	}
	
}
main {
	flex-grow : 1;
	margin : 0;
	width : 100%;
	padding : 1em 0;
	background-color : #fff;
	& > section {
		padding : 1em 0;
		margin-bottom : 2em;
	}
}
.page-header {
	background-color : whitesmoke;
	margin : 0 !important;
	width : 100% !important;
	padding : 2em 0;
	h1 {
		margin-top : 0 !important;
	}
}

nav {
	margin : 0;
	width : 100%;
	padding : 0;
	display : block;
	background-color : $navigation-color;
	color: white;
	
	ul {
		background-color : inherit;
		color: inherit;
		margin : 0;
		padding : 0;
		width : 100%;
		display : flex;
		flex-direction : column;
		justify-content : center;
		align-content : stretch;
		align-items : stretch;
		text-align : center;
		line-height : 1;
		position : absolute;
		top : 0;
		left : 0;
		right : 0;
		bottom : 0;
		z-index : 10;
		gap: 2em;
		li {
			display : block;
			margin : 0;
			padding : 0;
			width : 100%;
			line-height : 1;
			background-color : inherit;
			color: inherit;
			a,a:any-link {
				display          : block;
				margin           : 0 auto;
				width            : 75%;
				line-height      : 1;
				background-color : inherit;
				color            : inherit;
				text-align : center;
				border-radius : 2em;
				padding : 1em 0;
				font-size : x-large;
				text-decoration : none !important;
				$_this-color: $navigation-color;
				&:hover {
					background-color : hover-color($_this-color);
					text-decoration : none !important;
					color: white;
				}
				&:focus {
					background-color : focus-color($_this-color);
					text-decoration : none !important;
					color : white;
				}
				&:active {
					background-color : active-color($_this-color);
					text-decoration : none !important;
					color : white;
				}
			}
		}
	}
}
[aria-current='page'] {
	background-color : darken($navigation-color, 10%) !important;
	color: rgba(white,0.25) !important;
	cursor : not-allowed;
	pointer-events: none;
}
